/* ## Objects
--------------------------------------------- */
embed,
iframe,
img,
object,
video,
.wp-caption,
.wp-video-shortcode {
  max-width: 100%;
  display: block;
}

img {
  height: auto;
}

figure {
  margin: 0;
}

video {
  object-fit: cover;
}

@media (min-width: 768px) {
  .wp-video-shortcode {
    width: auto;
  }
}

.no-js .wp-video-shortcode {
  background-color: #f6f8fa;
  border: 1px solid #eceef1;
  width: 100%;
}

/* ## Tables
--------------------------------------------- */
table {
  background-color: #ffffff;
  border: 1px solid #eceef1;
  border-collapse: collapse;
  border-spacing: 0;
  line-height: 1;
  margin-bottom: 40px;
  margin-bottom: 4rem;
  width: 100%;
}

tbody {
  border-bottom: 1px solid #eceef1;
}

td,
th {
  text-align: left;
  padding: 0.618em;
  border-top: 1px solid #eceef1;
  border-right: 1px solid #eceef1;
}

/* ## Accessibility
--------------------------------------------- */
.screen-reader-text,
.screen-reader-text span,
.screen-reader-shortcut,
.wp-custom-logo .site-title,
.wp-custom-logo .site-description {
  position: absolute !important;
  background-color: #ffffff;
  clip: rect(0, 0, 0, 0);
  height: 1px;
  height: 0.1rem;
  width: 1px;
  width: 0.1rem;
  border: 0;
  overflow: hidden;
}

.screen-reader-text:focus,
.screen-reader-shortcut:focus {
  clip: auto !important;
  height: auto;
  width: auto;
  display: block;
  padding: 10px 20px;
  padding: 1rem 2rem;
  z-index: 100000;
  text-decoration: none;
  box-shadow: 0 0 0.2rem 0.2rem rgba(42, 49, 57, 0.1);
}

.more-link {
  position: relative;
  font-style: normal;
}

.genesis-skip-link {
  margin: 0;
}

.genesis-skip-link li {
  height: 0;
  width: 0;
  list-style: none;
}

/* ## Site Containers
--------------------------------------------- */
.boxed-page .site-container {
  box-shadow: 0 0 0 1px #eceef1, 0 0 10rem rgba(198, 202, 206, 0.1);
  max-width: 1152px;
  margin: 0 auto;
  background-color: #ffffff;
}

