    width: 88%;
    max-width: 1152px;
    padding-left: 0;
    padding-right: 0;
  }
}

.content-sidebar-wrap .wrap,
.wrap .wrap {
  max-width: 100%;
  width: auto;
  padding: 0;
}

.site-inner {
