  .contact-page.has-before-header .site-inner {
    padding-top: 14rem;
  }
}

.content-sidebar-wrap {
  max-width: 1152px;
  position: relative;
  z-index: 1;
}

@media (min-width: 896px) {
  .content-sidebar-wrap {
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;
  }
  .content-sidebar-wrap:before, .content-sidebar-wrap:after {
    display: none;
  }
  .sidebar-content .content-sidebar-wrap {
    flex-direction: row-reverse;
  }
}

/* ## Content Widths and Positions
--------------------------------------------- */
.content {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  padding: 40px 0;
  padding: 4rem 0;
  min-height: 400px;
  min-height: 40rem;
}

@media (min-width: 768px) {
  .content {
    padding: 6rem;
  }
}

.home.page .content {
  max-width: 100%;
  width: 100%;
  padding: 0;
  margin: 0;
}

@media (min-width: 768px) {
  .home.page .content {
    margin: 0;
  }
}

.blog .content,
.archive .content,
.search-results .content {
  margin-top: 0;
  padding: 40px 0;
  padding: 4rem 0;
  background-color: transparent;
}

@media (min-width: 768px) {
  .blog .content,
  .archive .content,
  .search-results .content {
    padding: 6rem 0;
  }
}

.blog .content:before,
.archive .content:before,
.search-results .content:before {
  display: none;
}

.home.page .content {
  background-color: transparent;
}

.page .content,
.single .content,
.error404 .content {
  position: relative;
  border-radius: 1px;
  background-color: #ffffff;
}

@media (min-width: 768px) {
  .page .content,
  .single .content,
  .error404 .content {
    margin: -6rem auto 0;
  }
  .page .content:before,
  .single .content:before,
  .error404 .content:before {
    display: block;
  }
}

.page .content:before,
.single .content:before,
.error404 .content:before {
  content: '';
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 400px;
  height: 40rem;
  box-shadow: 0 -5rem 10rem rgba(42, 49, 57, 0.05);
  pointer-events: none;
}

@media (min-width: 896px) {
  .single .content,
  .contact-page .content {
    width: 768px;
  }
}

@media (min-width: 896px) {
  .single-product .content {
    width: 896px;
  }
}

@media (min-width: 768px) {
  .landing-page .content {
    margin-top: 0;
  }
  .landing-page .content:before {
    display: none;
  }
}

.sidebar-primary {
  padding-bottom: 50px;
  padding-bottom: 5rem;
}

@media (min-width: 896px) {
  .content {
    width: 100%;
  }
  .sidebar-primary {
    width: 25%;
    padding-top: 3rem;
  }
  .blog .sidebar-primary,
  .archive .sidebar-primary {
    padding-top: 6rem;
  }
  .sidebar-content .content,
  .content-sidebar .content {
    width: calc(75% - 5rem);
    margin-left: 0;
    margin-right: 0;
  }
}

/* ## Column Classes (including fifths)
--------------------------------------------- */
