/* ## Menu Toggle
--------------------------------------------- */
.menu-toggle {
  padding: 1em 0.5em;
  margin-right: -0.5em;
  cursor: pointer;
  background: transparent;
  box-shadow: none;
  display: block;
  height: auto;
  position: relative;
  color: #2a3139;
  z-index: 1;
}

@media (min-width: 896px) {
  .menu-toggle {
    display: none;
  }
}

.menu-toggle:hover, .menu-toggle:focus {
  background: none;
  box-shadow: none;
  color: #2a3139;
}

.menu-toggle.activated,
.shrink .menu-toggle {
  color: #2a3139;
}

.menu-toggle.activated span,
.shrink .menu-toggle span {
  background: #2a3139;
}

.menu-toggle.activated span:before, .menu-toggle.activated span:after,
.shrink .menu-toggle span:before,
.shrink .menu-toggle span:after {
  background: #2a3139;
}

.menu-toggle span {
  display: block;
  float: right;
  position: relative;
  height: 2px;
  background: #2a3139;
  width: 20px;
  width: 2rem;
  border-radius: 3px;
}

.menu-toggle span::before, .menu-toggle span::after {
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #2a3139;
  border-radius: 3px;
  content: " ";
}

.menu-toggle span::before {
  top: -5px;
  top: -0.5rem;
  transition: top 0.2s 0.2s, transform 0.2s 0s;
}

.menu-toggle span::after {
  bottom: -5px;
  bottom: -0.5rem;
  transition: bottom 0.2s 0.2s, transform 0.2s 0s;
}

.menu-toggle.activated span {
  background: none;
}

.menu-toggle.activated span::before {
  top: 0;
  transform: rotate(45deg);
  transition: top 0.2s 0s, transform 0.2s 0.2s;
}

.menu-toggle.activated span::after {
  bottom: 0;
  transform: rotate(-45deg);
  transition: bottom 0.2s 0s, transform 0.2s 0.2s;
}

/* ## Sub Menu Toggle
--------------------------------------------- */
.sub-menu-toggle {
  position: absolute;
  right: 3em;
  height: auto;
  top: 0.1em;
  background: none;
  padding: 1em;
  margin: 0 -1em;
